body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100%;
  /* background-color: darkorange; */
}

.app * {
  /* don't allow the children of the scrollable element to be selected as an anchor node */
  overflow-anchor: none;
  /* width: 100%; */
}

#anchor {
  /* allow the final child to be selected as an anchor node */
  overflow-anchor: auto;

  /* anchor nodes are required to have non-zero area */
  height: 1px;
}

i {
  font-style: normal;
}

.UL {
  padding: 0;
  margin: 0;
}

.LI {
  list-style: none;
  margin-bottom: 20px;
}

.evaluatorList > li {
  margin-bottom: 40px;
}

.evaluatorSpace, .macroSpace {
  padding-top: 10px;
  width: 93vw;
  margin-left: 6vw;
  /* margin-bottom: 5em; */
  height: 86vh;
  /* overflow-y: scroll */
}

