  color: #1976d2;
}

#editorEnter:active {
  color: #164b80;
}


@media (min-aspect-ratio: 1/1) {
  #topbar {
    position: fixed;
    /* top: 0; */
    left: 0;
    /* width: 5%; */
    height: 100%;
    background-color: #1976d2;
    color: white;
    text-align: center;
    /* padding-top: 1em; */
    /* padding-left: 5vw; */
  }  
}

#input {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
  z-index: -1; 
}

.icon {
  /* min-width: 32px; */
  /* min-height: 32px; */
  color: white;
  margin: 20px;
  margin-bottom: 0px;
  cursor: pointer; /* "hand" cursor */
}

.iconLabel {
  font-size: 0.8em;
  /* margin-left: 7px; */
  margin-bottom: 20px;
}

/* Maximum aspect ratio */
@media (max-aspect-ratio: 1/1) {
  #topbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    /* height: 100%; */
    background-color: #1976d2;
    color: white;
    
    /* text-align: center; */
    /* padding-top: 1em; */
    /* padding-left: 5vw; */
  }
  #topbar p {
    display: none;
    /* display: inline-block; */
  }
  #topbar div {
    display: inline-block;
  }

  .icon {
    margin: 20px;
    /* margin-bottom: 0px; */
    cursor: pointer; /* "hand" cursor */
  }

  .evaluatorSpace, .macroSpace {
    margin-top: 10em;
    margin-left: 1vw;
    height: 70vh;
  }

  .editorContainer {
    bottom: 1px;
    width: 98vw;
    left: 1vw;
  }
  
  .editorSettings {
    position: fixed;
    top: 8vw;
    left: 0;
    margin-left: 20px;
    margin-top: 0vh;
  }
}
.box {
  position: relative;
  border-top: none;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 5px;
  font-size: 1.5em;

  border: 1px solid #eee;
  border-radius: .25rem;
}

.boxEval {
  border-left: 5px solid cornflowerblue;
}

.boxEval.inactiveBox {
  border-left: 5px solid rgb(167, 197, 255);
}

.boxMacro {
  border-left: 5px solid darkgoldenrod;
  padding-top: 2px;
  padding-bottom: 2px;
}

.boxNote {
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 1em;

}

.inactiveStep {
  color: gray;
}

.hiddenIcon, .hiddenMacroIcon {
  margin-left: 0.75em;
  display: none;
}

li:hover > .step > .inlineblock > .hiddenIcon {
  display: inline;
  color: black;
}

li:hover .hiddenMacroIcon {
  color: black;
  display: inline;
}

.controlBtn {
  --mdc-theme-primary: #1976d2;
  display: inline-block;
  font-size: 0.5em;
  margin-right: 1em;

}

.badge {
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border: 2px solid black;
  border-radius: 1rem;
  display: inline-block;
  font-size: 0.6em;
  margin-right: 1em;
}

.makeBadge {
  cursor: pointer;
}

.makeBadge:hover {
  background-color: #BDBDBD;
}

.inactiveMessage {
  color: gray;
  font-size: 0.6em;
  margin-left: 0;
}

.emptyStep {
  margin-top: 0;
  margin-bottom: 5px;

}
#controls {
  position: absolute;
  top: 0;
  right: 0;
  margin-bottom: 10px;
}

.enabled {
  cursor: pointer;
}

.disabled {
  cursor: auto;
  color: grey;
}

#controls i {
  padding-left: 5px;
  padding-right: 5px;
}

.badge > .strategies {
  display: none;
}

.badge:hover > .strategies {
  display: inline-block;
  border: none;
}

.badge:hover > .strategyName {
  min-width: 11em;
}

.badge .strategies {
  padding: 0;
  margin-top: 0px;
  padding: 0px;
}

.exerciseSwitch {
  font-size: 0.8em;
}
.step {
  /* font-size: 2em; */
  word-wrap: anywhere;
}

.left.redex {
  border-bottom: 2px solid #81c784;
}

.substitutedArg {
  background-color: #81c784;
  padding-left: 1px;
  padding-right: 1px;
  border-radius: 2px;
}

.right.redex {
/*  background-color: #ffa4a2; /* #ec407a */
  border: 2px solid #81c784;
  border-radius: 5px;
  /* color: white; */
  /* padding: 1px; */
}

.argument.substitutedArg {
  background-color: #539b57;
}

.activeStep .macro, .activeStep .argument, .activeStep .churchnumeral {
  cursor: pointer;
}

.macro.redex {
  background-color: #80d8ff;
  border-radius: 5px;
}

.churchnumeral.redex {
  background-color: #82b1ff;
  border-radius: 5px;
  /* padding: 1px; */

}

.breakpoint {
  /* border-bottom: 3px solid black; */
  background-color: #ff6090;
  border-radius: 5px;
}

.breakpoint.churchnumeral.redex, .breakpoint.macro.redex {
  border: 2px solid #ff6090;
}

