.lastReduction {
  color: gray;
  font-size: 0.7em;
  margin: 0;
  margin-left: 1.5em;
}

.stepNumber {
  /* display: none; */
  color: gray;
  display: inline-block;
  margin: 0;
  font-size: 0.5em;
  margin-right: 1em;
}

.stepMessage {
  color: gray;
  /* display: inline-block; */
  /* margin: 0; */
  font-size: 0.8em;
  margin-left: 1.5em;
}

.inlineblock {
  display: inline-block;
}

.boxExercise .activeStep .substitutedArg,
.boxExercise .activeStep .redex {
  border: none;
  background-color: white;
  padding: 0;
}
