#controls {
  position: absolute;
  top: 0;
  right: 0;
  margin-bottom: 10px;
}

.enabled {
  cursor: pointer;
}

.disabled {
  cursor: auto;
  color: grey;
}

#controls i {
  padding-left: 5px;
  padding-right: 5px;
}

.badge > .strategies {
  display: none;
}

.badge:hover > .strategies {
  display: inline-block;
  border: none;
}

.badge:hover > .strategyName {
  min-width: 11em;
}

.badge .strategies {
  padding: 0;
  margin-top: 0px;
  padding: 0px;
}

.exerciseSwitch {
  font-size: 0.8em;
}